.input {
  min-width: 200px;
  max-width: 200px;
  height: 24px;
  background: none;
  padding: 4px;

  &--wrapper {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
  }

  &--label {
    font-size: 0.8rem;
    margin-bottom: 0.6rem;
    letter-spacing: 0.025em;
  }

  &--error {
    border-color: red;

    &--message {
      padding: 4px;
      color: red;
      font-size: 12px;
      height: 22px;
    }
  }
}
